import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Constants } from '../@global/constants';
import { MenuController } from '@ionic/angular';
import { BasePage } from '../base/base.page';
import { Location } from '@angular/common';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { CommonState } from '../@store/common/common.state';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-onboarding-slides',
  templateUrl: './onboarding-slides.page.html',
  styleUrls: ['./onboarding-slides.page.scss'],
})
export class OnboardingSlidesPage extends BasePage implements OnInit, OnDestroy {
  public symbolPath = Constants.COMPLETE_LOGO_PATH;
  public logoPath = Constants.LOGO_PATH;
  public imagePathOnboarding = Constants.ONBOARDING_IMAGE_PATH;
  public language = '';
  private subscription$: Subscription;

  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private menuCtrl: MenuController
  ) {
    super(location, router, store, utilsService);
  }

  doIonViewWillEnter() {
    this.menuCtrl.enable(false);
  }

  async ngOnInit() {
    this.subscription$ = this.store.select(CommonState.getLanguage).subscribe((data) => {
      if (!data) {
        return;
      }

      this.language = data;
    });
  }

  public goLoginWithParams(showBack: boolean): void {
    this.utilsService.goLoginWithParams(showBack);
  }

  ngOnDestroy(): void {
    this.subscription$.unsubscribe();
  }
}
